<template>
  <div class="select-document-card-molecule">
    <el-card class="select-document-card-molecule__card">
      <div v-if="!document.showAddIcon" class="select-document-card-molecule__card__existing" v-on:click="selectDocument($event,document)">
        <h1 class="select-document-card-molecule__card__existing__header">{{document.data.title}}</h1>
        <label>Type : REST API</label>
        <label>Version : {{document.data.version}}</label>
        <div class="select-document-card-molecule__card__existing__toolbar">
            <el-tooltip class="item" effect="dark" content="Settings" placement="bottom">
              <img v-on:click="selectDocument($event,document,'/settings')" class ="settings" src="../../../../../Frontend/public/assets/toolbar/cog-solid.svg"/>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="API Documentation" placement="bottom">
              <img v-on:click="selectDocument($event,document,'/swagger')" class ="doc" src="../../../../../Frontend/public/assets/toolbar/file-alt-solid.svg"/>
            </el-tooltip>
        </div>
      </div>

      <div v-if="document.showAddIcon" class="select-document-card-molecule__card__new" v-on:click="createDocument()">
        <h1 class="select-document-card-molecule__card__new__header">New Project</h1>
        <div>
          <img src="../../../../../Frontend/public/assets/newitem.png"/>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import SelectDocumentCardMolecule from './SelectDocumentCardMolecule.hook'
const hook = new SelectDocumentCardMolecule();

export default {
  name: 'SelectDocumentCardMolecule',
  beforeCreate() { hook.hook(this)},
  methods: hook.methods("selectDocument","createDocument"),
  props: 
  {
     document:{}
  },
  data() {
      return { }
  }
}

</script>

<style scoped lang="scss">

.select-document-card-molecule__card:hover {
  background-color: #3a3a3a;
}

.select-document-card-molecule {
  
  > * {
    cursor:pointer;
  }

  width:100%;
  &__card{
    height:200px;
    border-radius: 5px;
    background-color: #333333;

    @mixin select-document-card-molecule__card__header(){
      margin-left:0px;
      margin-top:0px;
      margin-bottom:30px;
      font-size: 30px;
      font-weight: 100;      
    }


    &__existing{

      > * {
        cursor:pointer;
      }
    
      &__header {
        @include select-document-card-molecule__card__header();
        text-align: left;
        margin-bottom:20px;
      }

      label {
        display: block;
        margin-top:10px;
        opacity: 0.7;
        font-size: 14px;
      }
      

      &__toolbar{
        margin-top:35px;

        img:hover {
          opacity: 0.4;
        }

        img {
          height:30px;
          transition: 0.2s;
          margin-right:15px;
          opacity: 0.6;
        }

        img.settings 
        {
          height:27px;
        }

        img.doc 
        {
          height:26px;
        }

        img.delete 
        {
          height:25px;
        }
      }


    }

    &__new {

      &__header {
        @include select-document-card-molecule__card__header();
        text-align: center;
      }

      div {
        display: flex;
        flex-direction: row;
        margin-top:-50px;

        img {
          padding-top:40px;
          width:100px;
          margin:auto;
          opacity: 0.2;
        }
      }

    }
  }
}

</style>
