import { AtomicDesign } from '../../../../Frontend/Core/AtomicDesign';
import {EVENTS} from '../../../UiResources/events';
import {getLeftToolbarButtons, getTopToolbarButtons} from '../../../../Frontend/Helpers/toolbar';
import Strings from '../../../UiResources/strings';

export default class DocumentManagementPage extends AtomicDesign {


    addEventHandlers(){
        this.actions().setEditorProfile({});
        this.onGlobalEvent(EVENTS.DOCUMENT.SELECTED, this.selectDocument);
        this.onGlobalEvent(EVENTS.DOCUMENT.TEMPLATE_SELECTED, this.createDocument);
    }

    async createDocument(data){
        const {type, categoryKey, templateId,formData} = data;
        const {result,error} =  await this.backend(Strings.PROGRESS_INDICATOR.CREATING_DOCUMENT).createDocument(type, categoryKey, templateId,formData);

        if (!error){
            this.selectDocument({...result});
        }
    }

    async getAllDocuments(){
        
        this.actions().setOpenedDocument({result:null});

        this.actions().setSelectedItems({});
       
        const {result,error} = await this.backend(Strings.PROGRESS_INDICATOR.LOADING_DOCUMENTS).getAllDocuments();       
        
        if (!error){
            this.vue.allDocuments = [...result, {showAddIcon:true,id:0}];
            this.checkFirstTimeAccess();
        }else {
            await this.msgbox(Strings.ERROR_MESSAGES.MESSAGE_ALL_DOCUMENTS, this.MSGBOX_TYPE.OK ,Strings.ERROR_MESSAGES.TITLE_ALL_DOCUMENTS);
        }
    }

    async selectDocument(document){
        
        const response = await this.backend(Strings.PROGRESS_INDICATOR.LOADING_DOCUMENT).getDocument(document.id);

        const {result,error} = response;
        if (!error){
            this.actions().setOpenedDocument(response);
            const params = {staticCache:true, localStorage:true};
            const editorProfileResult = await this.backend(params).getEditorProfile(result.type);
            
            if (editorProfileResult?.result){
                let editorProfile = editorProfileResult?.result;
                editorProfile.leftBar = getLeftToolbarButtons(editorProfile.assets.leftToolBar);

                if (result.editables.length > 0){
                    const selectedEditableId = result.selectedResources?.selectedEditable;
                    if (selectedEditableId){
                        const editable = result.editables.find(editable => editable.id == selectedEditableId);
                        this.actions().patchSelectedItems({editable});
                    }else {
                        this.actions().patchSelectedItems({editable:result.editables[0]});
                    }
                    
                }

                this.actions().setEditorProfile(editorProfile);
                const route = document.route ? document.route : editorProfile.leftBar[0].route;
                this.vue.$router.push(route);
            }else {
                await this.msgbox(Strings.ERROR_MESSAGES.MESSAGE_ONE_DOCUMENTS, this.MSGBOX_TYPE.OK ,Strings.ERROR_MESSAGES.TITLE_ONE_DOCUMENTS);
            }
            
        }else {
            await this.msgbox(Strings.ERROR_MESSAGES.MESSAGE_ONE_DOCUMENTS, this.MSGBOX_TYPE.OK ,Strings.ERROR_MESSAGES.TITLE_ONE_DOCUMENTS);
        }
    }

    initToolbar(){
        const toolbarButtons = getTopToolbarButtons("FULLSCREEN");
        this.actions().setTopToolbar(toolbarButtons);
    }

    async checkFirstTimeAccess(){
        const firstTimeAlready = localStorage.getItem("firstTimeAlready");
        if (this.vue.firstTimeAccess && !firstTimeAlready){
            const message = `
                <p>Thank you for checking out less-code BETA, You also welcome to give us feedback to make less-code more better.
                </p>
                <br/>
                <p>To get started quickly you can checkout the following;</p>
                <ul>
                <li>Getting started video on <a style="color:white;" href="https://www.youtube.com/watch?v=i2T9JxR65uc" target="_blank">youtube</a></li>
                <li>And also our official <a style="color:white;" href="/#/documentation" target="_blank">documentation</a></li>
                </ul>
            `;

            this.vue.$alert(message, 'Welcome :)', {
                dangerouslyUseHTMLString: true
              });

            this.backend(null).uncheckFirstTimeAccess();
            localStorage.setItem("firstTimeAlready",true);
            this.vue.showWelcomeDialog = true;
        }
    }
}