<template>
  <div class="document-management-page">
    <div class="document-management-page__cardcontainer">
      <el-row :gutter="20">
        <transition-group name="list">
          <el-col :xs="24" :sm="12" :md="6" :lg="6" :xl="6" v-for="document in allDocuments" :key="document.id">
              <SelectDocumentCardMolecule :document="document" />
          </el-col>
        </transition-group>
      </el-row>
    </div>
  </div>
</template>

<script>
import DocumentManagementPage from './DocumentManagementPage.hook';
import SelectDocumentCardMolecule from './SelectDocumentCardMolecule/SelectDocumentCardMolecule';

const hook = new DocumentManagementPage();
hook.setBackendNamespace("Pages/Editor").setElementName("DocumentManagementPage");

export default {
  name: 'DocumentManagementPage',
  beforeCreate() { hook.hook(this); hook.addEventHandlers(); },
  mounted() { hook.getAllDocuments(); hook.initToolbar();},
  beforeDestroy(){ hook.clearToolBar(); hook.unsubscribeGlobalEvents();}, 
  methods: {},
  components: { 
    SelectDocumentCardMolecule,
  },
  data () {
    return {
      allDocuments:[]
    }
  },
  computed: {
    firstTimeAccess(){
      return this.authData.profile.firstTimeAccess;
    },
    ...hook.getters("authData")
  }
};

</script>

<style lang="scss">
.document-management-page{

  &__cardcontainer{
    margin:20px;

    .list-item {
      display: inline-block;
      margin-right: 10px;
    }
    .list-enter-active, .list-leave-active {
      transition: all 0.5s;
    }
    .list-enter, .list-leave-to {
      opacity: 0;
    }

    .el-card {
      margin-bottom:20px;
    }
  }
}
</style>
