import {AtomicDesign} from '../../../../../Frontend/Core/AtomicDesign';
import {TEMPLATE_TYPES} from "../../../../../Common/VariousTypes";
import {EVENTS} from '../../../../UiResources/events';

export default class HomeSelectDocumentMolecule extends AtomicDesign {

    selectDocument($event,data,route) {
        $event.stopPropagation();
        this.emitGlobalEvent(EVENTS.DOCUMENT.SELECTED, {...data,route});
    }

    createDocument() {
        this.emitGlobalEvent(EVENTS.TEMPLATE.SHOW_DIALOG,{
            type:TEMPLATE_TYPES.DOCUMENT,
            title:"Please select a template for your new document",
            callback: (data)=>{
                if (data){
                    this.emitGlobalEvent(EVENTS.DOCUMENT.TEMPLATE_SELECTED,data);
                }
            }
        });
    }
}